<template>
  <el-container>
    <el-aside width="200px">
      <el-menu
        default-active="1"
        class="el-menu-vertical-demo"
        @select="handleMenuSelect"
      >
        <el-menu-item index="1">基本信息</el-menu-item>
        <el-menu-item index="3">通知设置</el-menu-item>
      </el-menu>
    </el-aside>

    <el-main>
      <el-card>
        <template v-if="activeTab === '1'">
          <h2>基本信息</h2>
          <el-form :model="form" ref="formRef" label-width="120px">
            <el-form-item label="系统名称">
              <el-input v-model="form.name" />
            </el-form-item>
            <el-form-item label="系统版本">
              <el-input v-model="form.version" />
            </el-form-item>
            <el-form-item label="描述">
              <el-input type="textarea" v-model="form.detail" />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveBasicInfo">保存</el-button>
            </el-form-item>
          </el-form>
        </template>

    

        <template v-if="activeTab === '3'">
          <h2>通知设置</h2>
          <el-form :model="notificationForm" ref="notificationFormRef" label-width="120px">
            <el-form-item label="邮箱通知">
              <el-switch v-model="notificationForm.emailNotifications" />
            </el-form-item>
            <el-form-item label="短信通知">
              <el-switch v-model="notificationForm.smsNotifications" />
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveNotificationSettings">保存</el-button>
            </el-form-item>
          </el-form>
        </template>
      </el-card>
    </el-main>
  </el-container>
</template>

<script setup>
import axios from 'axios';
import { onMounted, ref } from 'vue';

const activeTab = ref('1');

// 表单数据
const form = ref({
  id: 0,
  name: '社区信息管理平台',
  version: 'v1.0-1',
  detail: '社区信息管理平台，科学帮助您管理社区信息，透明公开',
});



// 通知设置表单数据
const notificationForm = ref({
  emailNotifications: false,
  smsNotifications: false,
});

// 处理菜单选择
const handleMenuSelect = (index) => {
  activeTab.value = index;
};

// 获取基本信息
const fetchBasicInfo = async () => {
  try {
    const response = await axios.get('http://localhost:7000/after/basic-info');
    if (response.data.code === 1 && response.data.data.length > 0) {
      const data = response.data.data[0];
      form.value = {
        id: data.id,
        name: data.name,
        version: data.version,
        detail: data.detail,
      };
    }
  } catch (error) {
    console.error('获取基本信息失败:', error);
  }
};
// 组件挂载时获取基本信息
onMounted(() => {
  fetchBasicInfo();
});
</script>

<style scoped>
.el-container {
  height: 100vh;
}

.el-menu-vertical-demo {
  height: 100%;
  border-right: 1px solid #ebeef5;
}

.el-card {
  padding: 20px;
}
</style>
